export default {
    setHomeTop:({commit},payload)=>{
        // console.log(payload);
        
        commit('setHomeTop',payload)
    },
    changeFoot:({commit,state},payload)=>{
        commit('changeFoot',payload)
    },
    changeLoading:({commit},payload)=>{
        commit('changeLoading',payload)
    },
    addItem:({commit,state},payload)=>{
        //每次都是完全覆盖state
        // console.log(payload);
        let arr=[...state.shopDei]
        let find=false;
        // console.log(payload.payload._id);
        
        arr.forEach((item)=>{
            if(item._id == payload.payload._id){
                find=true
            }
        })
        // arr.forEach((item,index)=>{
        //     if(item._id==payload._id){
        //       item.number++;//数量递增
        //       find=true;
        //     }
        //   });
        // console.log(find);
        // console.log(payload.payload);
        
          if(!find) {
            //数量
            arr.push(payload.payload);
          }
        
        // if(find){
        //     arr.push(payload.payload)
        //     console.log(arr);
            
        //     commit("addItem",arr)
        // }
        // console.log(arr);
        commit("addItem",arr)
    },
    
    changeItem:({commit,state},payload)=>{
        let arr=[...state.shopDei]
        // console.log(payload.payload._id);
        arr.forEach((item)=>{
            if(item._id == payload.payload._id){
                // console.log(payload.num);
                
                if(payload.payload.num<0){
                    if(item.num==1){
                        item.num==1
                    }else{
                    item.num += payload.payload.num 
                    }
                }else{
                    item.num += payload.payload.num 
                }
            }
        })
        // console.log(arr);
        
        commit("changeItem",arr)
    },
    removeItem:({commit,state},payload)=>{
        let arr= [...state.shopDei]
        arr.forEach((item,index)=>{
            if(item._id==payload.payload._id){
                arr.splice(index,1)
            }
        })
        commit("removeItem",arr)
    }

}