<template>
  <div class="van-cell van-cell--borderless van-field app-header__search">
    <div class="van-field__left-icon">
        <i class="van-icon van-icon-search"><!----></i>
    </div>
        <div class="van-cell__value van-cell__value--alone">
            <div class="van-field__body">
                <input type="search" v-model="value" placeholder="请输入搜索关键词" @blur="blur" class="van-field__control" @keyup.13=onSearch>
            </div>
        </div>

</div>
</template>

<script>
// import { Cell, CellGroup } from 'vant';
// import { Cell, CellGroup } from 'vant';

// Vue.use(Cell).use(CellGroup);
export default {
    data(){
        return {
            value:''
        }
    },
    methods:{
      onSearch(){
        //   console.log(1);
        if(this.value==""){
            alert("请输入内容，再查询")
        }else{
            this.$router.push({name:'search',query:{q:this.value}})
        }
          
      },
      blur(){
          this.value=''
      }
    }
}
</script>
<style scoped>
.app-header__search{z-index:10;width: 100%;background:rgba(0,0,0,.1); height: 3.125rem;position: fixed;top: 0;left: 0;}
.van-field__control{border-radius: 20px;border: 1px solid #ccc;background: #fff;padding-left: .2rem}

</style>



