<template>
<div class="search">
    <top-title :info="info"></top-title>
    <div v-if="bl">
    <ul >
        <router-link v-for="item of bannerList"
        :key="item._id"
        :to="{name:'detail',query:{dataName:'banner'},params:{id:item._id}}"
        tag="li"
        >
            <img :src="item.url" alt="">
            <p>{{item.detail.des}}</p>
            <span>{{item.detail.newprice}}元</span>
        </router-link>
    
        <router-link v-for="item of businessList"
        :key="item._id"
        :to="{name:'detail',query:{dataName:'business'},params:{id:item._id}}"
        tag="li"
        >
            <img :src="item.url" alt="">
            <p>{{item.detail.des}}</p>
            <span>{{item.detail.newprice}}元</span>
        </router-link>
    
        <router-link v-for="item of List"
        :key="item._id"
        :to="{name:'detail',query:{dataName:'list'},params:{id:item._id}}"
        tag="li"
        >
            <img :src="item.url" alt="">
            <p>{{item.detail.des}}</p>
            <span>{{item.detail.newprice}}元</span>
        </router-link>
    </ul>
    </div>
    <div v-else class="gohome">
        <p>没有您想要搜索的数据,请重新搜索</p>
        <button @click="$router.push('home')">go-home</button>
        <!-- this.$router.push('home') -->
    </div>
</div>
</template>

<script>
import TopTitle from './TopTitle'
export default {
    components:{
        TopTitle,
    },
    data(){
        return {
            info:'',
            search:[],
            bannerList:[],
            businessList:[],
            List:[],
            bl:false
        }
    },
    watch:{
        search:{
            handler(newc,oldc){
                if(newc.length>0){
                        this.bl=true
                    }else{
                        this.bl=false
                    }    
            },
            deep:true            
        }
    },
    async beforeRouteEnter(to,from,next){
        let searchList=[]
        let bannerList=[]
        let businessList=[]
        let List=[]
        await axios({
            url:"/api/list",
            params:{_limit:100,q:to.query.q}
        }).then(res=>{
            if(res.data.err==0){
                List.push(...res.data.data)
                searchList.push(...res.data.data)
            }
        }),
        await axios({
            url:"/api/banner",
            params:{_limit:100,q:to.query.q}
        }).then(res=>{
            if(res.data.err==0){
                searchList.push(...res.data.data)
             bannerList.push(...res.data.data)
            }
        }),
        await axios({
            url:"/api/business",
            params:{_limit:100,q:to.query.q}
        }).then(res=>{
            if(res.data.err==0){
            searchList.push(...res.data.data)
            businessList.push(...res.data.data)
            }
        }),
        next((_this)=>{
                 _this.businessList=businessList
                 _this.bannerList=bannerList
                 _this.List=List
                 _this.search=searchList
                 _this.info=to.query.q
             })
        
    },

}
</script>
<style>
    .search ul{display: flex;flex-wrap: wrap;justify-content: space-around;margin-top: .5rem;}
    .search ul li{width: 48%;height: 14.1875rem;text-align: center;float:left}
    .search ul li img{width: 9.09375rem;height: 9.09375rem;}
    .search ul li p{font-size:11.25px;color:#4C4C4C;margin-top: 0.4em;text-overflow: ellipsis;white-space: nowrap;overflow: hidden;}
    .search ul li span{font-size:14px;display: block;color: #B81B22;margin: 0.4em auto;}
    .search .gohome{width: 100%;height:100%;margin-top: 10rem;}
    .search .gohome p{font-size: 20px;color:#999}
    .search .gohome button{margin-top: 2rem; font-size: 20px;background: orange;border-radius: 5px}
</style>

