<template>
    <div class="reg">
              <top-title :info="'注册'"></top-title>

        <!-- <van-uploader
        v-model="fileList"
        :max-count="1"
        class="reg-upload"
        ref='f1'
        >
         <van-button icon="photo" type="primary" >点击上传头像</van-button>
        </van-uploader> -->
            <input type="file" accept="image/*" ref="f1" id="upload" @change="fileup">
            <label for="upload" class="name" ref="f2">请上传图片</label>
        <van-cell-group class="reg-inp">
            <van-field class="reg-single"
                v-model="username"
                required
                clearable
                label="用户名"
                placeholder="请输入用户名"
            />
            <van-field class="reg-single"
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                required
            />
            <van-button class="reg-btn" type="danger" size="large" @click="reg">注册</van-button>
            </van-cell-group>
    </div>
</template>
<script>
import TopTitle from './TopTitle'
export default {
    data(){
        return{
            username:"",
            password:"",
            fileList:[]
        }
    },
    components:{
        TopTitle
    },

    methods:{
        fileup(el){
            // console.log( this.$refs.f2.innerHTML);
             this.$refs.f2.innerHTML="上传成功"
            //  this.$refs.f2
        },
        reg(){
             let formData = new FormData();
            formData.append('username',this.username)
            formData.append('password',this.password)

            // formData.append('icon',this.$refs.f1.files[0])
            // if(this.$refs.f1.files[0]){
            formData.append('icon',this.$refs.f1.files[0])
            // }
            // console.log(this.$refs.f1.files[0]);
            
            axios({
                url:'/api/reg',
                data:formData,
                method:'post'
                }).then((res)=>{
                    // console.log(res);
                    
                    res.data.err===0 ? this.$router.push('/login') : alert(res.data.msg)
                })
        }
    }
}
</script>

<style>
    .reg .reg-single{margin-bottom: 3rem}
    .reg .reg-btn{margin-top: 7rem}
    .reg .reg-upload{margin-left: 12rem}
    .reg #upload{display:none}
    .reg .name{display: block;width: 50%;text-align: center;height: 3rem;line-height: 3rem; background: cyan;border: 1px solid #ccc;border-radius: 5px;margin: 0 auto;}
</style>
