<template>
    <div class="all-order">
        <top-title :info="'全部订单'"></top-title>
        <ul>
            <li ref="li1" @click="activeItem('li1',$event)">全部</li>
            <li ref="li2" @click="activeItem('li2',$event)">配送中</li>
            <li ref="li3" @click="activeItem('li3',$event)">完成</li>
            <li ref="li4" @click="activeItem('li4',$event)">无效</li>
        </ul>
        <components :is='active'></components>
    </div>
</template>


<script>
import TopTitle from './TopTitle'
import Whole from './Whole'    //全部订单
import Invalid from './Invalid'   //无效订单
import Conduct from './Conduct'   //进行中
import Complete from './Complete'  //完成
export default {
    data(){
        return {
            active:''
        }
    },
    components:{
        TopTitle,Complete,Conduct,Invalid,Whole
    },
    methods:{
        activeItem:function(avg,el){
            let ali=document.querySelectorAll('ul li')
            for(let i =0;i<ali.length;i++){
                ali[i].style.color='#a5a5a5'
            }
            el.target.style.color='black'
            // console.log(avg);
            if(avg=='li1'){
                this.active='Whole'
            }else if(avg=='li2'){
                this.active="Conduct"
            }else if(avg=='li3'){
                this.active="Complete"
            }else if(avg=='li4'){
                this.active="Invalid"
            }
            
            
        }
    },
    beforeRouteEnter(to,from,next){
        if(to.query.type==1){
            next((_this)=>{
                _this.$refs.li1.style.color='black'
                _this.active='Whole'
            })
        }else if(to.query.type==2){
            next((_this)=>{
                _this.active="Conduct"
                _this.$refs.li2.style.color='black'
            })
        }else if(to.query.type==3){
            next((_this)=>{
                _this.active="Complete"
                _this.$refs.li3.style.color='black'
            })
        }else{
            next((_this)=>{
                _this.active="Invalid"
                _this.$refs.li4.style.color='black'
            })
        }


    }
}
</script>
<style>
    .all-order ul{width: 100%;display: flex;justify-content: space-around;height:3rem;}
    .all-order ul li{width: 25%;text-align: center;line-height: 3rem;color:#a5a5a5} 
</style>

