import Vue from 'vue';
import VueRouter from 'vue-router';
import store from './store'
Vue.use(VueRouter);

//import 是静态引入，编译时 ， 会事先引入

// import Slide from '../common/Slide'
// import AppHeader from '../common/AppHeader'

import Home from '../components/Home'
import Detail from '../components/Detail'
import Reg from '../components/Reg'
import Login from '../components/Login'
import User from '../components/User'
import ShopCar from '../components/ShopCar'
import Channel from '../components/Channel'
import SortChild from '../components/SortChild'
import Sort from '../components/Sort'
import Error from '../common/Error'
import AllOrder from '../components/AllOrder'
import Comment from '../components/Comment'
import Search from '../components/Search'
let routes = [
  {name:"home",path:'/home',component:Home},
  {name:"detail",path:'/detail/:id',component:Detail},
  {name:"reg",path:'/reg',component:Reg},
  {name:"login",path:'/login',component:Login},
  {name:"user",path:'/user',component:User},
  {name:"sortchild",path:'/sortchild',component:SortChild},
  {name:"shopcar",path:'/shopcar',component:ShopCar},
  {name:"channel",path:'/channel',component:Channel},
  {name:"allorder",path:'/allorder',component:AllOrder},
  {name:"comment",path:'/comment',component:Comment},
  {name:"search",path:'/search',component:Search},
  {name:"sort",path:'/sort',component:Sort},
  {path:'/',redirect:'/home'},
  {name:"error",path:'*',component:Error},
];

let router = new VueRouter({
  routes,
  scrollBehavior(to,from,savedPosition){
    // console.log(store.state.homeTop);
		return { x: 0, y: 0 }//对于所有路由导航，简单地让页面滚动到顶部
	}
});

export default router;