    <router-view></router-view>
    <app-footer v-if='bFoot'></app-footer>
  </div>
</template>

<script>
// import './assets/css/animate.css'
import ShopCar from './components/ShopCar'
import Channel from './components/Channel'
import AppHeader from './common/AppHeader'
import AppFooter from './common/AppFooter'
import Home from './components/Home'
import Reg from './components/Reg'
import Login from './components/Login'
import Detail from './components/Detail'
import User from './components/User'
import BLoading from './components/BLoading'
import BuSilder from './components/BuSilder'

import {mapGetters,mapState} from 'vuex'
export default {
  data(){
    return{
      bl:false
    }
  },
  components:{
    AppHeader,AppFooter,Home,Reg,Login,Detail,User,Channel,ShopCar,BLoading,BuSilder
  },
  computed:mapGetters(["bFoot",'bLoading']),
   watch:{
    $route:{
      handler({path}){
        // console.log('app',path)
        //home/follow/column true true
        //user/ false true
        //login/reg/detail false false
        if(/home|user|channel|sort|/.test(path)){
          // this.$root.$data.bFoot=true;
          this.$store.dispatch('changeFoot',true)
        }
        if(/detail|reg|login|shopcar/.test(path)){
          // this.$root.$data.bFoot=false;
          this.$store.dispatch('changeFoot',false)

        }
       

      },
      immediate:true
    }
  },
}
</script>

