<template>
<div class="detail">
       <top-title :info="'商品详情'"></top-title>
    
    <detail-silder class="detail-slide" :detailsilder="detailsilder" ></detail-silder>
    <div class="detail-pro">
        <span class="name">{{name}}</span>
        <div>
            <span class="oldprice">{{oldprice}}</span>
            <span class="newprice">{{newprice}}</span>
            <span class="discount">充100返100，点击充值</span>
        </div>
    </div>
    <van-divider></van-divider>
    <i class="van-icon van-icon-free-postage shipicon"></i>
    <span class="ship">全场购物满199元免运费 </span>
    <div class="van-goods-action">
        <div role="button" tabindex="0" class="van-goods-action-icon" @click="gocar()">
            <div class="van-icon van-icon-cart-o van-goods-action-icon__icon">
                <div class="van-info">{{showNum}}</div>
                </div>购物车</div>
                <button @click="addItem" class="van-button van-button--danger van-button--middle van-button--square van-goods-action-button">
                    <span class="van-button__text" >加入购物车</span>
                </button>
            </div>
</div>



</template>
<script>
// import Swiper from 'swiper'
import TopTitle from './TopTitle'
import DetailSilder from './DetailSilder'
import { mapGetters,mapState } from 'vuex';
export default {
    data(){
        return {
            shopNum:5,
            detailsilder:[],
            // detailDes:{},
            name:'',
            oldprice:"",
            newprice:'',
            goodsId:'',
            dataName:'',
            src:'',
            showNum:''
        }
    },
    computed:{
        ...mapState(['shopDei'])
    },
    components:{
        DetailSilder,TopTitle
    },
    methods:{
        gocar(){
             axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            this.$router.push('/shopcar')
            }else{
            // this.showNum=0
            this.$router.push('/login')
            }
        })
        },
        addItem(){
            // _id:this.goodsId,
            //         des:this.name,
            //         price:this.newprice,
            //         num:1,
            //         dataName:this.dataName,
            //         src:this.src,
            //         flag:false
            axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            this.goods=localStorage.getItem('goodsId');
            if(this.goods){
                // console.log(this.goods);
            this.goods=JSON.parse(this.goods)
            this.flag=true;
            // console.log(this.goods[0]._id);
            // console.log(this.goodsId);
            for(let i=0;i<this.goods.length;i++){
                if(this.goods[i]._id==this.goodsId){
                    alert('请勿重复购买')
                    this.flag=false
                }
            }
            if(this.flag){
                this.goods.push({
                    _id:this.goodsId,
                    des:this.name,
                    price:this.newprice,
                    num:1,
                    dataName:this.dataName,
                    src:this.src,
                    flag:false
                })
            }
        }else{
            this.goods=[
                {
                    _id:this.goodsId,
                    des:this.name,
                    price:this.newprice,
                    num:1 ,
                    dataName:this.dataName,
                    src:this.src,
                    flag:false

                }
            ]
        }
        this.$store.dispatch({type:'addItem',payload:{_id:this.goodsId,num:1,des:this.name,price:this.newprice,dataName:this.dataName,src:this.src,flag:false}})
        localStorage.setItem('goodsId',JSON.stringify(this.goods))
            }else{
            this.$router.push('/login')
            }
            
        })
        
        }
    },
    mounted(){
        axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            this.showNum=this.$store.state.shopDei.length
            }else{
            this.showNum=0
            }
        })
            // this.showNum=this.$store.state.shopDei.length
            // console.log(this.$store.state.shopDei.length);
            
    },
    beforeRouteEnter(to,from,next){
        // console.log(to.query.dataName);
        // console.log(to.params.id)
        
        axios({
            url:`/api/${to.query.dataName}/${to.params.id}`,
            // url:'/api/silder/5d42e51482b416472f61be4a'
            // params:{_id:to.params.id}
        }).then((res)=>{
            // console.log(res);
            // console.log(res.data.data.detail.url);
            next((_this)=>{
                _this.detailsilder=res.data.data.detail.url
                // console.log(res.data.data.detail.url);
                _this.name=res.data.data.des||res.data.data.detail.des
                _this.oldprice=res.data.data.detail.oldprice
                _this.newprice=res.data.data.detail.newprice
                _this.goodsId=res.data.data._id
                _this.dataName=to.query.dataName
                _this.src=res.data.data.url
                // console.log(res);
                
                })
            // _this.applist=res.data.data.detail.url;
            // next()
            
        })
        
    },
}
</script>

<style>
    
    .detail .detail-slide img{width:100%;height: 23.4375rem;}
    .detail .detail-pro {margin-left: 1.375rem;}

    .detail .ship{color: #4c4c4c;font-size: 16px;}
    .detail .shipicon{color: #4c4c4c;font-size: 16;margin-left: 1.375rem;}

    .detail .detail-pro .name{color: #4c4c4c;font-size: 12px;}
    .detail .detail-pro .oldprice{color: #c8161e; font-size: 12px; text-decoration: line-through;}
    .detail .detail-pro .newprice{color: #c8161e; font-size: 16px;}
    .detail .detail-pro .discount{display:block; color: #c8161e; font-size: 12px;}
</style>

