<template>
<div class="app-slider">
     <div class="swiper-container app-swiper">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item of applist" :key="item._id">
                <router-link :to="{name:'detail',params:{id:item._id},query:{dataName:'banner'}}" tag=div>
                    <!-- {{item._id}} -->
                    
                <img :src="item.url" alt="">
                <p class="des">{{item.des}}</p>
                <div class="price">
                <span class="delprice">{{item.price}}</span>
                <span class="curprice">充值相当于：<i>{{item.disprice}}</i>
                </span>
                </div>
             </router-link>
            </div>
        </div>
     
    </div>
</div>
</template>

<script>
import Swiper from 'swiper'

export default {
    props:['applist'],
    updated(){
        new Swiper ('.app-swiper', {
            slidesPerView : 'auto',
            // spaceBetween : '1%',
         })
    }
}
</script>

<style>
    .app-slider{width: 100%;height: 19.375rem;margin-top: .5rem;margin-left: 1rem;}
    .app-slider .swiper-slide{margin-right: .5rem;width: 10.1875rem;}
    .app-slider img{width: 10.1875rem;height: 13.125rem;margin-bottom: .3rem}
    .app-slider .des{color:#565656; font-size:12px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;text-align: left;}
    .app-slider .price .delprice{font-size:11.25px;display: block; color: #ccc; }
    .app-slider .price .curprice{font-size:11.25px;display: block;text-align: left }
    .app-slider .price .curprice i{font-style: normal;color: #b81c22}

</style>

