     </div>
</template>
<script>
import TopTitle from './TopTitle'
export default {
    data(){
        return {
            info:"个人中心",
            user:[]
        }
    },
    components:{
        TopTitle
    },
    methods:{
        logout(){
            axios({url:'api/logout'}).then((res)=>{this.$router.push('home')})
        },
        all(){
            console.log(1);
            
        // :to="{name:'allorder',query:{type:1}}"
            this.$router.push({name:"allorder",query:{type:1}})
        }
    },
    beforeRouteEnter(to,from,next){
        axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            next(_this=>_this.user = res.data.data)
            }else{
            next('/login')
            }
            
        })
       
    },
    // mounted(){
    //     // console.log(http://localhost:3000+this.user);
    // }
}
</script>

