<template>
    <div class="sort">
        <top-title :info="'分类'"></top-title>
        <div class="sort-list">
            <div class="sort-title">
                <ul>
                    <li @click="show('recommend',$event)" >推荐</li>
                    <li @click="show('man',$event)">男装</li>
                    <li @click="show('women',$event)">女装</li>
                    <li @click="show('manShoe',$event)">男鞋</li>
                    <li @click="show('womenShoe',$event)">女鞋</li>
                    <li @click="show('sock',$event)">袜品</li>
                    <li @click="show('residence',$event)">家居</li>
                    <li @click="show('wear',$event)">童装</li>
                    <li @click="show('shirt',$event)">内衣</li>
                </ul>
            </div>
            <div class="sort-cont">
                <ul>
                    <router-link
                    v-for="item of sortlist"
                    :key='item.id'
                    tag=li
                    :to="{name:''}"
                    >
                        <img :src="item.url" alt="">
                        <span>{{item.title}}</span>
                    </router-link>
                </ul>
            </div>
        </div>
       
    </div>
</template>
<script>
import TopTitle from './TopTitle'
export default {
    data(){
        return {
            sortlist:[],
            list:{}
        }
    },
    components:{
        TopTitle
    },
    methods:{
        show(avg,el){
            for(let i =0;i<el.target.parentNode.children.length;i++){
                el.target.parentNode.children[i].style.background="#e5e5e5"
            }
            el.target.style.background='white'
            // axios({
            //     url:'/api/sort'
            // }).then((res)=>{
            //     this.sortlist=res.data.data[0][`${avg}`]
            //     // console.log(res.data.data[0][`${avg}`]);
                
            // })
            this.sortlist=this.list[`${avg}`]
            
        }
    },
    mounted(){
        // document.getElementsByClassName('li')[0].style.background
        // this.$refs.li.$el.click()
        axios({
                url:'/api/sort'
            }).then((res)=>{
                this.list=res.data.data[0]
                // console.log(res.data.data[0]);
                this.sortlist=res.data.data[0].recommend
                // console.log(res.data.data[0][`${avg}`]);
                
            })
        // this.triggerEvent('show')
        
    }
}
</script>

<style>
    .sort .sort-list{background: white;width: 100%;overflow: hidden;}
    .sort .sort-list .sort-title{width:25%;float: left;}
    .sort .sort-list .sort-title ul li{ background: #e5e5e5;margin-top: .2rem;width:100%;height:2.3125rem;text-align: center;font:14px/37px "";}
    .sort .sort-cont{background: white;width:75%;float: left;margin-bottom: 4rem}
    .sort .sort-cont li{width: 33%;text-align: center;float: left;margin-top: .3rem}
    .sort .sort-cont li img{width:4.3125rem;height:4.3125rem;}
    .sort .sort-cont li span{color:#999;font-size: 12px;display: block}
</style>


