<template>
<div class="home">
    <app-header></app-header>
     <Slide class="home-slide" :silderlist=silderlist></Slide>
    <app-nav></app-nav>
    <sub-slider></sub-slider>
    <app-slider :applist=applist></app-slider>
    <List :list=list></List>
    <div class="btn" v-if="bl">
        <van-button type="primary" size="small" @click="$router.push('login')">登录</van-button>
        <van-button type="primary" size="small" @click="$router.push('reg')">注册</van-button>
    </div>
</div>
</template>
    
<script>
import Slide from '../common/Slide'
import AppHeader from '../common/AppHeader'
import SubSlider from '../common/SubSlider'
import AppNav from './AppNav'
import AppSlider from './AppSlider'
import List from './List'
import { mapGetters } from 'vuex';
let top =0
export default {
    data(){
        return {
            applist:[],
            silderlist:[],
            list:[],
            bl:false
        }
    },
    components:{
        List,AppSlider,AppNav,SubSlider,Slide,AppHeader
    },
    mounted(){
        axios({
            url:"/api/banner"
        }).then((res)=>{
            this.applist=res.data.data
            // console.log(res.data.date);
        }),
         axios({
            url:"/api/silder"
        }).then((res)=>{
            this.silderlist=res.data.data
            // console.log(res);
            // console.log(res.data.date[0]._id);
        }),
        axios({
            url:"/api/list",
            params:{_limit:100}
        }).then((res)=>{
            this.list=res.data.data
            // console.log(res);
        });
        // this.$nextTick(function () {
        //     // console.log(top);
            // console.log(this.$store.state.homeTop);
        //     //  document.documentElement.scrollTop=3000
        //     setTimeout(() => {
        //     document.documentElement.scrollTop =5000
        //     }, 200);
        // })
        // // setTimeout(() => {
        // document.documentElement.scrollTop =5000
        // }, 200);
        // console.log(top);
        // let top=this.$store.state.homeTop
        // document.documentElement.scrollTop=5000
        // console.log(this.top);
        // console.log(this.$store.state.homeTop);
    },
    beforeRouteEnter(to,from,next){
        axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            next(_this=>_this.bl = false)
            }else{
            next(_this=>_this.bl = true)
            }
            
        })
    },
    // destroyed(){
    //     destroyed(){
    //         console.log(document.documentElement.scrollTop);
    //     // this.$root.$el.scrollTop
    //     // top=document.documentElement.scrollTop;
    //     //  let top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
    //     // this.$store.dispatch('setHomeTop',document.documentElement.scrollTop)
    // },
    beforeRouteLeave(to,from,next){
        // let position=window.scrollTop();
        // console.log(position);
        top= document.documentElement.scrollTop;
        this.$store.dispatch('setHomeTop',top)
        // console.log(document.documentElement.scrollTop);
        
        next()
        
    }
}
</script>
<style>
   .home{margin-bottom: 3.5rem}
   .home .btn{display: flex;justify-content: space-around;background: rgb(233, 230, 230);align-items: center;height:6rem}
    .home .home-slide img{display:block;width:100%;height: 15.5625rem;}

</style>


