<template>
<div class="shop-car">
    <top-title :info="'购物车'"></top-title>
    <div v-if="bl">
    <div class="car">
        <div class="tip">
            <span>在售商品:</span>
            <div class="button">
            <button @click=all>全选</button>
            <button @click=alldel>删除</button>
           </div>
        </div>
        <ul>
            <li
            v-for="item of shopDei"
            :key=item._id
            >
                <input type="checkbox" @change="checkShop()" v-model="item.flag" :id=item._id class="check" ref="check1">
                <img :src="item.src" alt="">
                <div class="shop-detail">
                <p>{{item.des}}</p>
                <span>￥{{item.price}}</span>
                    <div class="count" :id=item._id>
                        <button  @click="red">-</button>
                        <input type="text" v-model="item.num">
                        <button  @click="inc">+</button>
                    </div>
                </div>
                <i class="delete van-icon van-icon-delete" @click="del" :id=item._id><!----></i>
            </li>
        </ul>
    </div>
    <div class="van-submit-bar__bar sum">
        <div class="van-submit-bar__text">
            <span>合计：</span>
            <span class="van-submit-bar__price">¥ {{count}}</span>
        </div>
            <button class="van-button van-button--danger van-button--large van-button--square van-submit-bar__button">
                <span class="van-button__text">结算</span>
            </button>
    </div>
    </div>
    <div v-else class="gohome">
        <p>您还未购买商品,前往首页选购商品</p>
        <button @click="gohome">go-home</button>
    </div>
</div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'

import TopTitle from './TopTitle'
import $ from '../assets/js/1.12.4'
export default {
    data(){
        return {
        shopDei:[],
        bl:false,
        sum:'',
        arr:[],
        result:0,
        }
    },
    components:{
        TopTitle
    },
    watch:{
        shopDei:{
            handler(newc,oldc){
                if(newc.length>0){
                        this.bl=true
                    }else{
                        this.bl=false
                    }    
            },
            deep:true            
        }
    },
    computed:{
        count(){
            var result=0
            let arr=[...this.shopDei]
            // console.log(arr);
            // console.log(this.$refs.check1);
            // for(let i=0;i<document.getElementsByClassName('check').length;i++){
            //     if(document.getElementsByClassName('check')[i].checked===true){
            //         arr.splice[i,1]
            //         // this.id=this.$refs.check1[i].getAttribute('id');
            //         // console.log(id);
            //         // result += this.shopDei[i].num*this.shopDei.price
            //     }
            // }
            // arr.forEach((item)=>{
            //     result+=item.price*item.num
            // })
            // return result;
            // this.shopDei.forEach(item => {
            //     for(let i=0;i<document.getElementsByClassName('check').length;i++){
            //     if(document.getElementsByClassName('check')[i].checked===true){
                        
            //     }
            // }
            // });
            // return this.sum 
            // var result=0;
            this.shopDei.forEach((item,index) => {
                if(item.flag){
                result += item.price * item.num;
                }
                });
                return result;
        }
       
    },
    methods:{
        gohome(){
            this.$router.push('home')
            
        },
        checkShop(){
            //   this.arr=[...this.shopDei];
            //   for(let i=0;i<document.getElementsByClassName('check').length;i++){
            //     if(document.getElementsByClassName('check')[i].checked===false){
            //         this.id=document.getElementsByClassName('check')[i].getAttribute("id")
            //         for(let k=0;k<this.arr.length;k++){
            //                     if(this.id==this.arr[k]._id){
            //                     this.arr.splice(k,1)
            //                 }
            //             }
            //     }
            // }
            // this.arr.forEach((item,index) => {
            // this.result += item.price * item.num;
            // });
        },
        del(el){
            this.id=el.target.getAttribute("id")
            this.$store.dispatch({type:'removeItem',payload:{_id:this.id}})
            // console.log(el.target);
            this.goods=JSON.parse(localStorage.getItem('goodsId'))
            for(let i=0;i<this.goods.length;i++){
                if(this.id==this.goods[i]._id){ 
                    this.shopDei.splice(i,1)  
                    this.goods.splice(i,1)
                    localStorage.setItem('goodsId',JSON.stringify(this.goods))
                }
            }
        },
        alldel(){
            for(let i=0;i<document.getElementsByClassName('check').length;i++){
                if(document.getElementsByClassName('check')[i].checked===true){
                    this.id=document.getElementsByClassName('check')[i].getAttribute("id")
            this.$store.dispatch({type:'removeItem',payload:{_id:this.id}})
                    this.goods=JSON.parse(localStorage.getItem('goodsId'))
                    for(let j=0;j<this.goods.length;j++){
                        if(this.id==this.goods[j]._id){
                            this.goods.splice(j,1) 
                            localStorage.setItem('goodsId',JSON.stringify(this.goods))
                            for(let k=0;k<this.shopDei.length;k++){
                                 if(this.id==this.shopDei[k]._id){
                                    this.shopDei.splice(k,1)

                            }
                        }
                        }

                    }
                    // console.log(i);
                    // console.log(this.shopDei[i]);
                    // // console.log(localStorage.getItem('goodsId'));
                    // // console.log(JSON.parse(localStorage.getItem('goodsId')));
                   
                    // console.log(this.goods[i]);
                    
                }
            }
        },
        all(){
            // for(let i=0;i<document.getElementsByClassName('check').length;i++){
            //     document.getElementsByClassName('check')[i].checked=true;
            // }
            this.shopDei.forEach((item)=>{
                item.flag=true;
            })
        },
        inc(el){
            this.id=el.target.parentNode.getAttribute("id")
        this.$store.dispatch({type:'changeItem',payload:{_id:this.id,num:1}})
            this.goods=JSON.parse(localStorage.getItem('goodsId'))
            for(let i=0;i<this.goods.length;i++){
                if(this.id==this.goods[i]._id){ 
                    this.shopDei[i].num++   
                    this.goods[i].num++
                    localStorage.setItem('goodsId',JSON.stringify(this.goods))
                }
            }
        },
        red(el){
         this.id=el.target.parentNode.getAttribute("id")
        this.$store.dispatch({type:'changeItem',payload:{_id:this.id,num:-1}})
            this.goods=JSON.parse(localStorage.getItem('goodsId'))
            for(let i=0;i<this.goods.length;i++){
                if(this.id==this.goods[i]._id){ 
                    if(this.shopDei[i].num==1){
                        this.shopDei[i].num=1
                        this.goods[i].num=1
                    }else{
                        this.shopDei[i].num--  
                        this.goods[i].num--
                    }
                    localStorage.setItem('goodsId',JSON.stringify(this.goods))
                }
            }
        }
    },
    mounted(){
        // console.log(this.$store.state.shopDei);
        
        this.goods= localStorage.getItem('goodsId')
        if(this.goods){
            this.shopDei=JSON.parse(this.goods)
        }
    },
    beforeRouteEnter(to,from,next){
        axios({
            url:'/api/user',
            method:'post'
        }).then((res)=>{
            if(res.data.err===0){
            next()
            }else{
            next('/login')
            }
            
        })
       
    },
    // updated(){
    //     this.goods= localStorage.getItem('goodsId')
    //     if(this.goods){
    //         this.shopDei=JSON.parse(this.goods)
    //     }
    // }
}
</script>
<style>
.shop-car{width: 100%}
.shop-car .car .tip{height: 1.8125rem;font-size:12px;width: 80%;margin: 0 auto}
.shop-car .car .tip span{float: left;}
.shop-car .car .tip .button{float:right;text-align: center}
.shop-car .car .tip .button button{color:#fff; border: none;height: 1.25rem;width: 3.5rem;margin-left: .5rem;border-radius: 5px;background: #b81c22}
.shop-car .car ul{margin-top: .5rem}
.shop-car .car ul li{ height:5.66rem;width:95%;margin: 0 auto; display: flex;color:#4c4c4c;font-size:12px;}
.shop-car .car ul li .check{cursor: pointer;width:1rem;height: 1rem;}
.shop-car .car ul li img{width: 5.25rem;height: 5.25rem;}
.shop-car .car ul li .shop-detail{width:11.625rem;}
.shop-car .car ul li .shop-detail span{margin-top: .4rem;display: block}
.shop-car .car ul li .shop-detail .count{margin-top: .6rem;}
.shop-car .car ul li .shop-detail .count input{width:1.875rem;height:1.6rem;margin: 0 .3rem}
.shop-car .car ul li .shop-detail .count button{width:1.875rem;height:1.6rem;}
.shop-car .car ul li .delete{font-size:25px;margin: 0 auto;line-height:5.66rem}
.shop-car .sum{position: fixed;right: 0;bottom: 0;z-index: 1;}
.shop-car .gohome{width: 100%;height:100%;margin-top: 10rem;}
.shop-car .gohome p{font-size: 20px;color:#999}
.shop-car .gohome button{margin-top: 2rem; font-size: 20px;background: orange;border-radius: 5px}
</style>

