<template>
<div class="login">
       <top-title :info="'登录'"></top-title>    
    
     <van-cell-group class="login-inp">
            <van-field class="login-single"
                v-model="username"
                required
                clearable
                label="用户名"
                placeholder="请输入用户名"
            />
            <van-field class="login-single"
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                required
            />
            <van-button class="login-btn" type="danger" size="large" @click="login">登录</van-button>
            <van-button class="login-go" type="default" size="small" @click="goreg">免费注册</van-button>

            </van-cell-group>

</div>
</template>

<script>
import TopTitle from './TopTitle'
export default {
    data(){
        return{
            username:"",
            password:""
        }
    },
    components:{
        TopTitle
    },
    methods:{
        login(){
            axios({
                url:'/api/login',
                method:'post',
                data:{username:this.username,password:this.password}
            }).then((res)=>{
                if(res.data.err==0){
                    this.$router.push('user')
                    // console.log(1);
                    
                }else{
                    alert(res.data.msg)
                }
            })
        },
        goreg(){
            this.$router.push('reg')
        }
    }
}
</script>

<style>
    .login .login-header{background: #b81c22;color: white;font:20px/46px "";margin-bottom: 5rem}
    .login .login-header .login-icon{font-size: 30px;color: white;line-height: 46px}
    .login .login-single{margin-bottom: 3rem}
    .login .login-btn{margin-top: 7rem}
</style>
