<template>
<div class="sort-child">
    <top-title :info="'商务衬衫'"></top-title>
    <business :bulist=bulist>
    <bu-silder slot="businessT"></bu-silder>
    </business>

</div>
</template>

<script>
import TopTitle from './TopTitle'
import Business from './Business'
import BuSilder from './BuSilder'

export default {
    data(){
        return {
            bulist:[]
        }
    },
    components:{
        TopTitle,Business,BuSilder
    },
    beforeRouteEnter(to,from,next){
        axios({url:`/api/${to.query.dataName}`,params:{_limit:100}}).then(
            (res)=>{
                // console.log(res.data.data)
                next((_this)=>{_this.bulist=res.data.data})
                }
        )
    }
}
</script>
<style>

    .sort-child  .img{width:100%;height: 1.875rem;}

</style>



